@import "../../Vars.scss";

.hidden {
  top: -100px;
  transition: 0.3s linear;
}

.btn {
  color: $green;
  border: 1px solid $green;
  margin-left: 10px;
  padding: 0.75rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  background-color: rgba(100, 255, 218, 0.02);
  transition: $transition;
  font-family: $font-mono;
  &:hover {
    background-color: rgba(100, 255, 218, 0.08);
  }
}

nav {
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0px;
  padding: 0.5rem 2rem;
  background-color: $blue;
  color: $white;
  font-family: $font-mono;
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 5px -1px rgba(0, 0, 0, 0.08);
  transition: 0.3s linear;
  z-index: 1;

  img {
    filter: invert(81%) sepia(31%) saturate(666%) hue-rotate(103deg)
      brightness(106%) contrast(104%);
    width: 40px;
    transition: all 0.3s ease-in-out;
    &:hover {
      transform: scale(1.2);
    }
  }

  .links {
    .item {
      display: inline;
      padding: 0 1rem;
      counter-increment: item 1;
      font-size: 14px;
      .link {
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        &::before {
          content: "0" counter(item) ".";
          margin-right: 5px;
          color: $green;
          font-size: 12px;
        }

        &:hover {
          color: $green;
        }
      }
    }
  }

  .hamburger {
    display: none;
    z-index: 2;
    cursor: pointer;
  }

  .hamburger_inner {
    display: none;

    background: $green;
    display: inline-block;
    position: relative;
    width: 30px;
    height: 1.5px;
    transform: scaleX(-1);

    &::before {
      background: $green;
      content: "";
      position: absolute;
      top: -10px;
      width: 120%;
      height: 1.5px;
    }

    &::after {
      background: $green;
      content: "";
      position: absolute;
      top: 10px;
      width: 80%;
      height: 1.5px;
    }
  }
}

@media only screen and (max-width: 762px) {
  nav {
    .links {
      width: 80%;
      height: 100vh;
      position: fixed;
      top: 0;
      right: -80%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 50px;
      z-index: 1;
      background-color: $light-blue;
      transition: $transition;
      &.open {
        right: 0;
      }
      .link::before {
        text-align: center;
        display: block;
      }
      .item {
        display: block;
        font-size: 18px;
      }
    }
    .hamburger {
      display: block;
      margin: 20px;
    }
  }
}
