@import "../../Vars.scss";

.blind {
  filter: blur(5px) brightness(0.7);
  transition: $transition;

}

#content {
  max-width: 1000px;
  margin: 0 auto;

  section {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    min-height: 100vh;
    padding: 0 2rem;

    h1 {
      font-family: $font-mono;
      font-size: clamp(14px, 5vw, 16px);
      font-weight: 400;
      color: $green;
      margin: 0 0 30px 4px;
    }

    h2,
    h3 {
      font-size: clamp(40px, 8vw, 50px);
      font-weight: 600;
      margin: 10px 0 0;
    }

    h2 {
      color: $lightest-slate;
    }

    p {
      margin: 20px 0 0;
      font-size: 18px;
      max-width: 540px;
    }

    a {
      margin: 50px 0 0;
    }
  }
}
