@import "../../Vars.scss";

.sideward {
  > .left {
    position: fixed;
    left: 40px;
    bottom: 0;

    ol {
      list-style: none;
      li a svg {
        width: 20px;
        margin: 10px;

        stroke: $lightest-slate;
        transition: $transition;

        &:hover {
          stroke: $green;
          transition: $transition;
        }
      }
      &::after {
        content: "";
        display: block;
        width: 1px;
        height: 90px;
        margin: 0px auto;
        background-color: $lightest-slate;
      }
    }
  }
  & > .right {
    position: fixed;
    right: 40px;
    bottom: 0;
    ol {
      list-style: none;

      li a {
        writing-mode: vertical-rl;
        color: $lightest-slate;
        font-size: 15px;
        text-decoration: none;
        transition: $transition;
        padding: 10px 0;
        font-family: $font-mono;

        &:hover {
          color: $green;
          transition: $transition;
        }
      }
      &::after {
        content: "";
        display: block;
        width: 1px;
        height: 90px;
        margin: 0px auto;
        background-color: $lightest-slate;
      }
    }
  }
}

@media only screen and (max-width: 762px) {
    .sideward{
        display: none;
    }
}